import tabBar from '../../common/tabbar.vue';
import http from '../../common/http.js';
export default {
	data() {
		return {
			winHeight:0,
			scrollHeight: 0,
			adsHeight: 0,
			iconHeight: 0,
			searchViewHeight: 0,
			searchViewTop: 0,
			searchViewBottom: 0,
			firstNavTop: 0,
			secondNavLabelHeight: 0,
			iconSize: 0,
			keyword: '',
			isIphoneX: false,
			navList: [],
			contentHeight: 0
		}
	},
	mounted() {
		this.isIphoneX = getApp().globalData.isIphoneX;
		//this.getNavList();
	},
	components:{
		tabBar
	},
	computed:{
		
	},
	methods: {
		getNavList() {
			const data = getApp().globalData;
			const apiPrefix = data.serverUri;
			const auth = data.auth;
			let _self = this;
			const url = apiPrefix + "?mod=loveword&ac=get_all_cid";
			http.request(url, {
				auth: auth
			}).then( resp => {
				console.log('resp pages/index/index:', resp);
				const n = resp.length;
				for(let j=0;j<n;j++) {
					let secondNavList = resp[j].secondNav;
					let secondNavLen  = secondNavList.length;
					for(let m=0;m<secondNavLen;m++) {
						// navClass
						if((m+1)%3 == 0) {
							if(m>2) {
								resp[j].secondNav[m].navClass = ' second-nav-label second-nav-lable-top'; 
							} else {
								resp[j].secondNav[m].navClass = ' second-nav-label '; 
							}
						} else {
							if(m>2) {
								resp[j].secondNav[m].navClass = ' second-nav-label second-nav-lable-margin second-nav-lable-top'; 
							} else {
								resp[j].secondNav[m].navClass = ' second-nav-label second-nav-lable-margin '; 
							}
						}
					}
				}
				_self.navList = resp;			
			});
		},
		// 跳转到关键词页面
		searchKeyword() {
			if(!this.keyword || this.keyword.length<2) {
				uni.showToast({
					title: '请输入关键词，且至少2个字符长度!',
					icon:"none",
					duration: 2000
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/index/huashu?keyword='+this.keyword
			});
		},
		handleContact() {
			console.log('handler...');
		},
		search() {
			
		},
		upgrade_vip() {
			// 升级
			let userInfo = this.$store.getters.userInfo;
			console.log('userInfo', userInfo);
			if (userInfo.nickName === undefined) {
				uni.showModal({
					title: '提示',
					content: '请先登录',
					showCancel: false,
					cancelText: '',
					confirmText: '确定',
					success: res => {
						if(res.confirm) {
							console.log('res...confirm');
							uni.navigateTo({
								url: '/pages/user/index'
							});
						}
					},
					fail: () => {},
					complete: () => {
						
					}
				});
				return;
			}
			// 投诉建议
			uni.navigateTo({
				url: '/pages/user/upgrade_user_vip'
			});
		},
		complaint() {
			// 投诉建议
			uni.navigateTo({
				url: '/pages/user/complaint'
			});
		},
		help_center() {
			uni.navigateTo({
				url: '/pages/index/meme'
			});
		},
		contact_consumer() {
		
		},
		scroll() {
			
		},
		lower() {
			let _self = this;
			let view = uni.createSelectorQuery().select("#content-view");
			view.boundingClientRect(data => {
				console.log('height:', data.height);
				//_self.contentHeight = data.height+88;
			}).exec();
		},
		upper() {
			
		},
		enter_huashu(title, navId) {
			uni.navigateTo({
				url:'/pages/index/huashu?title='+encodeURIComponent(title)+'&navId='+navId
			});
		}
	},
	onLoad() {
		let winHeight = uni.getSystemInfoSync().windowHeight;
		let winWidth = uni.getSystemInfoSync().windowWidth;
		let ratio     = winHeight/731;
		ratio = ratio.toFixed(2);
		this.iconHeight = 102; 
		this.adsHeight= 175;
		this.winHeight = winHeight;
		this.searchViewHeight = 40;
		this.searchViewTop    = 20;
		this.searchViewBottom = 0;
		this.firstNavTop = 20;
		this.secondNavLabelHeight = 30;
		this.iconSize = this.searchViewHeight - 10;
		this.contentHeight = winHeight*750/winWidth - 175 - 102 - 80 ;
		this.getNavList();
	}
}
